import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Group } from 'src/app/classes/group/group';

@Injectable({
  providedIn: 'root'
})
export class GroupDataService {

  constructor(private http:HttpClient){}

  create(group:Group){
    return this.http.post<any>('http://localhost:3000/group/create', group);
  }

  retrieve(value){
    return this.http.post<any>('http://localhost:3000/group/retrieve/one', {"name":value});
  }

  retrieveAll(){
    return this.http.get<any>('http://localhost:3000/group/retrieve/all');
  }

  update(oldGroupname, group:Group){
    return this.http.post<any>('http://localhost:3000/group/update', {"oldGroupname":oldGroupname, "group":group});
  }

  delete(name){
    return this.http.post<any>('http://localhost:3000/group/delete', {"name":name});
  }
}
